@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;700&display=swap);
.random__button{display:flex;justify-content:center;align-items:center;margin:20px 20px 20px auto;background-color:transparent;border:none;font-weight:500;font-style:normal;font-size:18px;color:#4f4f4f;padding:10px 20px;cursor:pointer}.random__button:hover{padding:9px 19px;border:1px solid #4f4f4f}.random__image{width:20px;margin-left:7px}
.footer{width:100%;text-align:center}.footer__txt{font-size:14px;color:#000}
.quote{width:100%;display:flex;justify-content:center;align-items:center;flex-direction:column;font-weight:500;font-size:36px}@media(max-width: 700px){.quote{font-size:26px}}.quote__text{max-width:614px;height:auto;display:flex;justify-content:center;align-items:center}.quote__text p{display:block;max-width:614px;border-left:2px solid #f7df94;padding:0 40px;margin:40px 0}@media(max-width: 700px){.quote__text p{padding:0 20px}}.quote__link{width:100%;max-width:614px;text-decoration:none;color:#000;padding:40px;transition:.3s;margin-top:50px}.quote__link:hover{background:#333;color:#fff}.quote__author{display:flex;flex-direction:column;justify-content:center;align-items:center;transition:.3s}.quote__name{margin:10px 0;font-size:24px;font-weight:700}.quote__genre{font-size:14px;margin:10px 0;color:#828282}.back-button{display:flex;justify-content:center;align-items:center;margin:20px 20px 20px auto;background-color:transparent;border:none;max-width:200px;font-weight:500;font-style:normal;font-size:18px;color:#4f4f4f;padding:10px 20px;text-decoration:none}.back-button:hover{padding:9px 19px;border:1px solid #4f4f4f}
body{display:flex;justify-content:center;align-items:space-between;font-family:"Raleway",sans-serif}.container{max-width:1000px;margin:50px auto 0;min-height:90vh}
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}*,*::before,*::after{box-sizing:border-box}
